.about__img {
    margin-bottom: 1.5rem;
    width:300px;
    height:320px;
    transform: rotate(-10deg);
    margin-left: 80px;
    box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 15px 0px;
 margin-top:50px;   
 padding :1rem;
 padding-bottom: 3rem;

}
#headyy{
    margin-left: 30px;
    margin-bottom: 50px;
}
#headyyimg{
    border: 1px solid transparent;
    margin-left: 30px;
}

.about{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    border: 1px solid transparent;
}
.about__img_name{
    text-align: center;
    font-family:monospace
}
.section_title{
    font-size: 40px;
}
.about__container{
    grid-template-columns:3fr 5fr;
    column-gap: 1.875rem;
}
.about__data {
    padding :1.875rem;
    background-color: var(--container-color);
    
    border-radius: var(--border-radius);
    column-gap:1.875rem;
    height: 300px;
    margin-top: 60px;
    align-items:flex-start;
    position: relative;
} 
#hello{
    margin-top: -50px;
    font-size:50px;
}
#user-detail-intro{
    text-align: justify;
   
}
.btn:hover{
    color: black;
}
.about__info{
    margin-top: -50px;
    
}

@media screen and (max-width:768px) {
    .about__container{
        grid-template-columns: repeat(1,1fr);
        font-size: 15px;
    }
    #user-detail-intro{
        font-size:12px;
        font-family:Arial, Helvetica, sans-serif;
    
        
    }
    #newintro{
        width:80%;

        text-align: justify;
    }
  
    
  }
  @media screen and (max-width: 426px) {
    .about__container{
        grid-template-columns: repeat(1,1fr);
        font-size: 15px;
    }
    #intro{
        font-size:12px;
        font-family:Arial, Helvetica, sans-serif;
  
    }
    #newintro{
        width:100%;

        text-align: justify;
    }
    .about__img {
     display: none;
    }
    .about__data{
        margin-top: -120px;
    }
    

  }
  @media screen and (max-width: 1077px) {
    
    #user-detail-intro{
        font-size:15px;
        font-family:Arial, Helvetica, sans-serif;
    
        
    }
     }
   
  





     @media screen and (min-width:395px) and (max-width:825px){
        .about{
            display: grid;
            grid-template-columns: repeat(1,1fr);
            border: 1px solid transparent;
            
            }
            #headyy{
                align-items: center;
             }
            
        
    }
    
    @media screen and (max-width:395px) {
     
      
    .about{
    display: grid;
    grid-template-columns: repeat(1,200px);
    border: 1px solid transparent;
    }
       
   
    }